#headers {
  align-items: flex-end;
}
#headers a {
  display: block;
  background-color: #dadada;
  margin: 0 6px;
  display: inline-block;
  color: #333;
  line-height: 40px;
  height: 40px;
  border-radius: 10px;
}
#headers a.sure {
  font-weight: bold;
  margin-top: 0;
  padding: 0 20px;
  line-height: 50px;
  height: 50px;
  background-color: #bfbfbf;
}
#index .contentbox {
  background-color: #f2f2f2;
  margin: 10px;
  height: calc(100vh - 70px);
  border-radius: 10px;
  position: relative;
}
#index .contentbox .hidebtn {
  position: absolute;
  right: -20px;
  z-index: 0;
  top: 50%;
  transform: translate(0 -50%);
  cursor: pointer;
}
#index .contentbox .leftbar.hide {
  width: 0;
}
#index .contentbox .leftbar,
#index .contentbox .rightbar {
  background-color: #d9d9d9;
  width: 220px;
  height: 100%;
  border-radius: 10px;
  padding: 10px;
  box-sizing: border-box;
  flex-direction: column;
  position: relative;
}
#index .contentbox .leftbar .btnline button,
#index .contentbox .rightbar .btnline button {
  height: 28px;
  border: none;
  padding: 0 10px;
  background-color: #bfbfbf;
  line-height: 28px;
  cursor: pointer;
  display: block;
  margin: 4px auto;
}
#index .contentbox .leftbar .line,
#index .contentbox .rightbar .line {
  flex-wrap: nowrap;
  margin: 4px 0;
}
#index .contentbox .leftbar .line input,
#index .contentbox .rightbar .line input {
  width: 100%;
}
#index .contentbox .leftbar .line button,
#index .contentbox .rightbar .line button {
  height: 28px;
  border: none;
  padding: 0 10px;
  background-color: #bfbfbf;
  line-height: 28px;
  cursor: pointer;
  display: block;
  margin: 4px auto;
}
#index .contentbox .leftbar .line .result,
#index .contentbox .rightbar .line .result {
  height: 28px;
  border: none;
  padding: 0 10px;
  background-color: #bfbfbf;
  line-height: 28px;
  cursor: pointer;
  display: block;
}
#index .contentbox .leftbar .desc,
#index .contentbox .rightbar .desc {
  width: 100px;
}
#index .contentbox .leftbar .desc select,
#index .contentbox .rightbar .desc select {
  width: 96px;
}
#index .contentbox .leftbar .tit,
#index .contentbox .rightbar .tit {
  width: 100px;
  color: #000;
  align-items: center;
  text-align: center;
  font-weight: bold;
}
#index .contentbox .leftbar .conlist,
#index .contentbox .rightbar .conlist {
  background-color: #bfbfbf;
  border-radius: 10px;
  flex-grow: 1;
}
#index .contentbox .leftbar .conlist .tit,
#index .contentbox .rightbar .conlist .tit {
  line-height: 40px;
  text-align: center;
  width: 100%;
  font-weight: bold;
}
#index .contentbox #next {
  position: absolute;
  right: 10px;
  bottom: 10px;
  z-index: 999;
  padding: 10px 20px;
  border-radius: 10px;
  background-color: #bfbfbf;
}
#index .contentbox #prev {
  position: absolute;
  left: 10px;
  bottom: 10px;
  z-index: 999;
  padding: 10px 20px;
  border-radius: 10px;
  background-color: #bfbfbf;
}
